.message {
    position: relative;
    z-index: 1;
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.15);
    padding: 15px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

#light__main__message {
    border: 1px rgb(5, 29, 66, 0.5) solid;

    .message__title {
        color: #051d42;
    }

    .message__field {
        input {
            border: 1px solid rgba(5, 29, 66, 0.5);
            color: #051d42;
            background-color: white;

            &:focus {
                border: 1px solid rgba(5, 29, 66, 0.3);
            }

            &:hover {
                border: 1px solid rgba(5, 29, 66, 0.3);
            }
        }

        input[type="text"] {
            color: #051d42;
        }

        textarea {
            border: 1px solid rgba(5, 29, 66, 0.5);
            color: #051d42;
        }
    }
}

.message__field {
    position: relative;

    img {
        position: absolute;
        width: 20px;
        right: 25px;
        top: 15px;
        opacity: 0.8;
    }

    input {
        width: 444px;
        border: 1px solid rgba(255, 255, 255, 0.15);
        border-radius: 15px;
        outline: none;
        height: 50px;
        background: none;
        padding-left: 20px;
        font-size: 16px;
        color: rgba(255, 255, 255, 0.8);
        font-family: "Roboto", sans-serif;
        background-color: #051d42;

        &:focus {
            border: 1px solid rgba(255, 255, 255, 0.3);
        }

        &:hover {
            border: 1px solid rgba(255, 255, 255, 0.3);
        }
    }

    input[type="text"] {
        font-size: 16px;
        color: rgba(255, 255, 255, 0.8);
        font-family: "Roboto", sans-serif;
    }

    textarea {
        width: 444px;
        border: 1px solid rgba(255, 255, 255, 0.15);
        border-radius: 15px;
        outline: none;
        height: 130px;
        background: none;
        padding-left: 20px;
        font-size: 16px;
        color: rgba(255, 255, 255, 0.8);
        font-family: "Roboto", sans-serif;
        padding-top: 10px;
        resize: none;
    }
}

.message__title {
    margin: 0 0 0 5px;
    font-size: 22px;
    color: rgba(255, 255, 255);
    font-family: "Roboto", sans-serif;
}

.message__btn {
    width: 180px;
    height: 56px;
    border-radius: 8px;
    background-color: #2e9ff8;
    border: none;
    color: white;
    font-size: 15px;
    font-weight: 600;
    font-family: "Roboto", sans-serif;
    transition: all 0.3s;

    &:hover {
        background-color: #1190f5;
        cursor: pointer;
    }
}

@media (max-width: 1200px) {}

@media (max-width: 768px) {
    .message {
        position: relative;
        z-index: 1;
        border-radius: 15px;
        border: 1px solid rgba(255, 255, 255, 0.15);
        padding: 15px;
        display: flex;
        flex-direction: column;
        gap: 15px;
    }

    .message__field {

        input {
            width: 300px;
            height: 40px;

        }

        textarea {
            width: 300px;
            height: 100px;
        }

        img {
            top: 8px;
            opacity: 0.8;
        }
    }

    .message__btn {
        width: 140px;
        height: 50px;

    }
}

@media (max-width: 480px) {
    .message__field {
        img {
            top: 4px;
            opacity: 0.8;
        }

        input {
            width: 246px;
            height: 30px;
        }

        input[type="text"] {
            font-size: 14px;
        }

        textarea {
            width: 246px;
        }

        textarea[type="text"] {
            font-size: 14px;
            height: 90px;
        }
       
    }
    .message__btn {
        width: 100px;
        height: 40px;
        font-size: 12px;
    }

    .message__title {
        margin: 0 0 0 5px;
        font-size: 20px;
        color: rgba(255, 255, 255);
        font-family: "Roboto", sans-serif;
    }
}